import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { OrderPage } from '../pages/order/order';
import { PricingPage } from '../pages/pricing/pricing';
import { HowItWorksPage } from '../pages/how-it-works/how-it-works';
import { FrequentlyAskedQuestionsPage } from '../pages/frequently-asked-questions/frequently-asked-questions';
import { ContactUsPage } from '../pages/contact-us/contact-us';
import { BecomeRunnerPage } from '../pages/become-runner/become-runner';
import { TermsOfServicePage } from '../pages/terms-of-service/terms-of-service';
import { RunnerInfoPage } from '../pages/runner-info/runner-info';
import { AuthService } from '../services/auth.service';
import { User } from '../models/user';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = OrderPage;

  pages: Array<{title: string, component: any, name: string, requiresLogin: boolean}>;

  user: User;

  isBrowser:boolean = false;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen,
  public auth: AuthService) {
    this.initializeApp();

    this.pages = [
      { title: 'Place An Order', component: OrderPage, name: 'OrderPage', requiresLogin: false },
      { title: 'Pricing', component: PricingPage, name: 'PricingPage', requiresLogin: false },
      { title: 'How It Works', component: HowItWorksPage, name: 'HowItWorksPage', requiresLogin: false },
      { title: 'Become A Runner', component: BecomeRunnerPage, name: 'BecomeRunnerPage', requiresLogin: false },
      { title: 'Frequently Asked Questions', component: FrequentlyAskedQuestionsPage, name: 'FrequentlyAskedQuestionsPage', requiresLogin: false },
      { title: 'Contact Us', component: ContactUsPage, name: 'ContactUsPage', requiresLogin: false },
      { title: 'Terms', component: TermsOfServicePage, name: 'TermsOfServicePage', requiresLogin: false },
      { title: 'Runner Info', component: RunnerInfoPage, name: 'RunnerInfoPage', requiresLogin: true }
    ];
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();

      this.auth.user.subscribe(user => this.user = user);
      
      this.isBrowser = document && document.URL.startsWith('http');
    });
  }

  openPage(page) {
    if((this.platform.is('core') || this.platform.is('mobile'))){ /// yeah a hack ... i think this is causing the Runnner Info page to load twice...
      this.nav.push(page.name);// this seems to be a hack, using to update the url
    }

    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  login(){
    this.auth.googleLogin();
  }

  logout(){
    this.auth.signOut();
  }
}



