import { Injectable } from '@angular/core';
import { User } from '../models/user';
import { StateProvider } from '../providers/state/state';

import * as firebase from 'firebase/app';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFirestore , AngularFirestoreDocument } from 'angularfire2/firestore';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/switchMap';
import { of } from 'rxjs';

@Injectable()
export class AuthService {

  user: Observable<User>;

  constructor(private afAuth: AngularFireAuth, private afs: AngularFirestore, private state: StateProvider){

      this.user = this.afAuth.authState
        .switchMap(user => {
          if(user){
            return this.afs.doc<User>(`users/${user.uid}`).valueChanges();
          } else {
            return of(null);
          }
        });
  }

  googleLogin(){
    const provider = new firebase.auth.GoogleAuthProvider();
    return this.oAuthLogin(provider);
  }

  signOut(){
    this.afAuth.auth.signOut();
  }

  toggleCheckinCheckout(user: User){ // this probably doesn't belong in here ...
    this.updateUserData({
      uid: user.uid,
      availableToRun: !user.availableToRun
    });

    this.state.get();
  }

  private oAuthLogin(provider){
    return this.afAuth.auth.signInWithPopup(provider)
      .then((credential) => {

        // maybe will need to change if add other providers?
        const user: User = {
          uid: credential.user.uid,
          email: credential.user.email,
          displayName: credential.user.displayName,
          photoURL: credential.user.photoURL,
          phone: credential.user.phoneNumber
        };

        this.updateUserData(user);
      })
  }

  private updateUserData(user, merge: boolean = true){
    const userRef: AngularFirestoreDocument<User> = this.afs.doc(`users/${user.uid}`);
    return userRef.set(user, { merge: merge });
  }

  canViewCheckinButton(user: User): boolean {
    if(!user) return false;
    const allowed = ['runner'];
    return this.checkAuthorization(user, allowed)
  }

  canViewRunnerInfoPage(user: User): boolean {
    if(!user) return false;
    const allowed = ['runner'];
    return this.checkAuthorization(user, allowed)
  }

  // determines if user has matching role
  private checkAuthorization(user: User, allowedRoles: string[]): boolean {
    if (!user) return false
    for (const role of allowedRoles) {
      if ( user[role] ) {
        return true
      }
    }
    return false
  }
}



