import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { ToastController } from 'ionic-angular';
import { RunnerApplication } from '../../models/runner-application';
import { ConfigProvider } from '../config/config';
import { BehaviorSubject } from "rxjs/BehaviorSubject";

@Injectable()
export class BecomeRunnerProvider {

  private runnerApplicationState = new BehaviorSubject({
    isSubmitting: false
  });

  constructor(private http: Http, private toastController: ToastController, private config: ConfigProvider) {}

  post(runnerApplication: RunnerApplication){

    this.runnerApplicationState.next({
      isSubmitting: true
    });

    this.http.post(this.config.baseApiOrderUrl + this.config.stage + this.config.becomeRunnerEndpoint, runnerApplication, {})
    .toPromise().then(result => {
      
      this.runnerApplicationState.next({
        isSubmitting: false
      });

      runnerApplication.clear();

      this.toastController.create({
        message: this.config.becomeRunnerSuccessMessage,
        duration: this.config.becomeRunnerSuccessMessageDuration
      }).present();
    }).catch(error =>{
      console.error(error);
      this.runnerApplicationState.next({
        isSubmitting: false
      });
      this.toastController.create({
        message: 'An error occurred. Please try again.',
        duration: 5000
      }).present();
    });
  }

  isSubmitting(){
    return this.runnerApplicationState.map(messageState => messageState.isSubmitting);
  }
}



