import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HttpModule } from '@angular/http';
import { OrderModule } from '../pages/order/order.module';
import { PricingModule } from '../pages/pricing/pricing.module';
import { HowItWorksModule } from '../pages/how-it-works/how-it-works.module';
import { FrequentlyAskedQuestionsModule } from '../pages/frequently-asked-questions/frequently-asked-questions.module';
import { ContactUsModule } from '../pages/contact-us/contact-us.module';
import { BecomeRunnerModule } from '../pages/become-runner/become-runner.module';
import { RunnerInfoModule } from '../pages/runner-info/runner-info.module';
import { TermsOfServiceModule } from '../pages/terms-of-service/terms-of-service.module';

import { OrderProvider } from '../providers/order/order';
import { ConfigProvider } from '../providers/config/config';
import { StateProvider } from '../providers/state/state';
import { ContactProvider } from '../providers/contact/contact';
import { BecomeRunnerProvider } from '../providers/become-runner/become-runner';

import { AngularFireModule } from 'angularfire2';
import { firebaseConfig } from '../environment';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreModule } from 'angularfire2/firestore';
import { AuthService } from '../services/auth.service';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpModule,
    OrderModule,
    PricingModule,
    HowItWorksModule,
    FrequentlyAskedQuestionsModule,
    ContactUsModule,
    BecomeRunnerModule,
    RunnerInfoModule,
    TermsOfServiceModule,
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFirestoreModule,
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    OrderProvider,
    ConfigProvider,
    StateProvider,
    ContactProvider,
    BecomeRunnerProvider,
    AngularFirestore,
    AuthService
  ]
})
export class AppModule {}



