import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { StateProvider } from '../../providers/state/state';
import { ConfigProvider } from '../../providers/config/config';
import { AuthService } from '../../services/auth.service';
import { Http } from '@angular/http';
import { Observable } from 'rxjs';
import { auth } from 'firebase';

@IonicPage()
@Component({
  selector: 'page-runner-info',
  templateUrl: 'runner-info.html',
})
export class RunnerInfoPage {

  private runnerInfo: Observable<any>;

  constructor(public navCtrl: NavController, public navParams: NavParams, public stateProvider: StateProvider,
    public config: ConfigProvider, public http: Http, public auth: AuthService) {

      this.auth.user.subscribe(user => {
        if(!auth.canViewRunnerInfoPage(user)){
          navCtrl.setRoot('OrderPage');
        }
      });
          //not sure why this is being hit twice ..... sucky for now.... oh well
        this.http.get(this.config.baseFirestoreFunctionsUrl + this.config.getRunnerInfoEndpoint, {})
        .toPromise().then(availableRunnersResponse => {
          this.runnerInfo = availableRunnersResponse.json();
        })
        .catch(error =>{
          console.error(error);
        });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RunnerInfoPage');
  }

  getRunnerImgUrl(url, size){ // probably really janky and may need to get rid of the pictures to avoid unnecessary data use
    let pieces = url.split("/");
    if(pieces[pieces.length - 2] == "mo"){
      pieces.splice(pieces.length -2, 1);
    }
    pieces.splice(pieces.length - 1, 0, 's'+size);
    return pieces.join("/");
  }

}



