export class RunnerApplication {

   name: string;
   email: string;
   phone: string;
   availability: string;
   duration: string;
   reason: string;

   constructor() {}

   clear(): void{
     this.name = null;
     this.email = null;
     this.phone = null;
     this.availability = null;
     this.duration = null;
     this.reason = null;
   }
}



