import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ConfigProvider {

  feeAmount: number = 7;
  public mainPhoneNumber: string = '208-425-2599';
  public baseApiOrderUrl: string = 'https://2vv8rth4gd.execute-api.us-east-2.amazonaws.com';
  public stage: string =  '/prod'; // dev
  public baseFirestoreFunctionsUrl: string = 'https://us-central1-get-me-that-delivery.cloudfunctions.net';
  public stateEndpoint: string = '/state';
  public orderEndpoint: string = '/order';
  public contactUsEndpoint: string = '/contact-us';
  public becomeRunnerEndpoint: string = '/become-runner';
  public getAvailableRunnersEndpoint: string = '/getAvailableRunners';
  public getRunnerInfoEndpoint: string = '/getRunnerInfo';
  public orderSuccessMessage: string = 'Thanks for your order! A runner will send you a text shortly and coordinate your delivery.';
  public orderSuccessMessageDuration: number = 10000;
  public contactUsSuccessMessage: string = 'Message sent!';
  public contactUsSuccessMessageDuration: number = 5000;
  public becomeRunnerSuccessMessage: string = 'Application sent! Please allow a few days for us to get back to you. Thanks for your interest!';
  public becomeRunnerSuccessMessageDuration: number = 7000;

  constructor(public http: Http){}
}



