@IonicPage()
@Component({
  selector: 'page-become-runner',
  templateUrl: 'become-runner.html',
})
export class BecomeRunnerPage {

  private runnerApplication = new RunnerApplication();

  constructor(public navCtrl: NavController, public navParams: NavParams, public becomeRunnerProvider: BecomeRunnerProvider) {}
}



