import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { ConfigProvider } from '../config/config';
import { StoreState } from '../../models/storeState';

@Injectable()
export class StateProvider {

  private storeState: BehaviorSubject<StoreState> = new BehaviorSubject<StoreState>({
    open: true,
    orderFormOverlayHtml: ''
  });

  constructor(public http: Http, private config: ConfigProvider) {}

  get(){// i need to figure out how to call this again when the app is reopned on a phone but hasn't been killed... open it, store closed, use different app, reopen app, store open, but doesn't refresh ....
    this.http.get(this.config.baseApiOrderUrl + this.config.stage + this.config.stateEndpoint, {})
    .toPromise().then(result => {
      // todo, clean up api to send it back like this and then i wouldn't have to map it

      const awsResult = result.json();

      if(awsResult.storeState.storeOpen){
        this.http.get(this.config.baseFirestoreFunctionsUrl + this.config.getAvailableRunnersEndpoint, {})
        .toPromise().then(availableRunnersResponse => {
          const availableRunners = availableRunnersResponse.json();

          this.storeState.next({
            open: awsResult.storeState.storeOpen && availableRunners.length,
            orderFormOverlayHtml: result.json().storeState.closedMessageHtml
          });
        })
        .catch(error =>{
          console.error(error);
        });
      } else {
        this.storeState.next({
          open: false,
          orderFormOverlayHtml: result.json().storeState.closedMessageHtml
        });
      }
    })
    .catch(error =>{
      console.error(error);
    });
  }

  isStoreOpen(){
    return this.storeState.map(storeState => storeState.open);
  }

  getStoreClosedMessage(){
    return this.storeState.map(storeState => storeState.orderFormOverlayHtml);
  }
}



